.wrapper-sr {
  max-width: 1480px;
  padding: 0 20px;
  margin: 0 auto;
  color: #000;
}

.wrapper-sr h1, .wrapper-sr h2, .wrapper-sr h3, .wrapper-sr h4, .wrapper-sr h5, .wrapper-sr h6 {
  text-transform: none;
  font-style: normal;
  letter-spacing: normal;
  line-height: normal;
}

.wrapper-sr h1 {
  font-size: 40px;
}

.wrapper-sr h2 {
  font-size: 22px;
  line-height: 1.5;
}

.wrapper-sr h3 {
  font-size: 18px;
}

.wrapper-sr h4 {
  font-size: 16px;
}

.wrapper-sr p {
  font-size: inherit;
  font-weight: inherit;
}

.wrapper-sr .sr-pic-block {
  display: block;
  font-size: 0;
}

.wrapper-sr .sr-pic-fullwidth {
  width: 100%;
}

.wrapper-sr .sr-desc-block {
  margin: 20px 0;
  padding: 20px 0;
}

.wrapper-sr .sr-desc-block h1, .wrapper-sr .sr-desc-block p {
  padding: 0px 80px;
}

.wrapper-sr .sr-desc-block p {
  padding-bottom: 20px;
  font-size: 20px;
  line-height: 1.6em;
}

.wrapper-sr .sr-desc-block-center {
  text-align: center;
}

.wrapper-sr .sr-desc-block-list li {
  list-style: none;
  font-size: 18px;
  font-weight: normal;
}

.wrapper-sr .sr-desc-row {
  display: flex;
  flex-wrap: wrap;
  min-height: inherit;
  max-height: inherit;
  justify-content: space-between;
}

.wrapper-sr .sr-desc-row > div {
  display: flex;
}

.wrapper-sr .sr-desc-col-4 {
  width: calc(33.333333333333336% - 6.666666666666667px);
}

@media (max-width: 1024px) {
  .sr-pic-block {
    width: 100%;
  }
  .wrapper-sr .sr-desc-block {
    margin: 16px 0;
    padding: 16px 0;
  }
  .wrapper-sr .sr-desc-block p {
    padding: 0 60px;
  }
}

@media (max-width: 767px) {
  .wrapper-sr .sr-desc-col-4 {
    width: 100%;
  }
  .wrapper-sr .sr-desc-block h1, .wrapper-sr .sr-desc-block p {
    padding: 0 20px;
  }
  .wrapper-sr h1 {
    font-size: 28px;
  }
  .wrapper-sr .sr-desc-block p {
    font-size: 16px;
  }
}
